export default class Proyectos extends Component {
  constructor(props) {
    super(props);

    this.cardWidth = this.props.screenWidth

    this.state = {
      focusedElement: 1,
      hoveredContent: null
    };
  }

  handleFocus(index) {
    if (index != this.state.focusedElement) {
      this.setState({ focusedElement: index })
    }
  }

  onHoverContent(index) {
    this.setState({ hoveredContent: index + 1 })
  }
  onHoverOutContent() {
    this.setState({ hoveredContent: null })
  }

  render() {
    return (
      <ProyectosDiv
        onMouseEnter={() => this.props.onMouseEnter()}
        sectionFocus={this.props.sectionFocus} isMobile={this.props.isMobile}>
        <SectionNav section={2} isMobile={this.props.isMobile} focus={this.props.sectionFocus == 2} onSelect={() => this.props.onMouseEnter()} />

        <div style={{ visibility: this.props.sectionFocus == 2 ? 'visible' : 'hidden', width: '100%', height: '90%', padding: 0, overflow: 'hidden', position: 'absolute', top: '10%' }}>
          {
            list.map((proy, index) => (
              <AnimatedCard
                key={index}
                isMobile={this.props.isMobile}
                focusedElementIndex={this.state.focusedElement}
                elementIndex={index + 1}
                hoveredIndex={this.state.hoveredContent}
                onMouseEnter={() => this.handleFocus(index + 1)}
                cardWidth={this.cardWidth}
                innerMargin={this.innerMargin}
                onMouseEnter={() => this.onHoverContent(index)}
                onMouseLeave={() => this.onHoverOutContent()}>
                {
                  !this.props.isMobile ?
                  <div style={{
                    position: 'absolute', width: '100%', height: '100%', overflow: 'hidden', borderRadius: '10px', backgroundColor: '#2b2b2b'
                  }}>
                    <AnimCardImg img={proy.img} hoveredContent={this.state.hoveredContent == index + 1} />
                  </div>
                  : null
                }
                <div style={{
                  width:  this.props.isMobile ? '95%' : '80%',
                  height: this.props.isMobile || index + 1 == this.state.hoveredContent && index + 1 == this.state.focusedElement ? '80%' : '20%',
                  position: 'absolute',
                  top: this.props.isMobile || index + 1 == this.state.hoveredContent && index + 1 == this.state.focusedElement ? '15%' : '40%',
                  left: this.props.isMobile ? 0 : '10%',
                  backgroundColor: 'rgba(255,255,255,0.8)',
                  transition: 'all 0.5s',
                  background: '#000000',
                  background: '-webkit-linear-gradient(to bottom, rgba(0,0,0,1), rgba(11, 6, 10, 0.9), rgba(14, 13, 21, 0.8), rgba(12, 20, 28, 0.8), rgba(4, 26, 33, 0.8), rgba(4, 32, 37, 0.7), rgba(4, 37, 40, 0.6), rgba(7, 43, 42, 0.5))',
                  background: 'linear-gradient(to bottom, rgba(0,0,0,1), rgba(11, 6, 10, 0.9), rgba(14, 13, 21, 0.8), rgba(12, 20, 28, 0.8), rgba(4, 26, 33, 0.8), rgba(4, 32, 37, 0.7), rgba(4, 37, 40, 0.6), rgba(7, 43, 42, 0.5))',
                }} >
                  <div style={{
                    display: 'flex',
                    flexDirection: 'column',
                    justifyContent:  'space-around',
                    alignItems: 'center',
                    height: '100%',
                    width: '100%'
                  }}>
                    <Typography variant="h1" style={{ fontSize: '1.5em', color: '#fff', fontWeight: 300 }}>{proy.titulo.toUpperCase()}</Typography>
                    {!this.props.isMobile && index + 1 != this.state.hoveredContent && index + 1 ? 
                    <KeyboardArrowDownOutlined  style={{ color: '#fff', fontSize: 36 }}/>
                    : null
                    }
                    <DevTextDescription show={this.props.isMobile || index + 1 == this.state.hoveredContent && index + 1 == this.state.focusedElement}>
                      <div style={{ overflowY: 'auto' }}>
                        <Typography variant="subtitle1" style={{ fontSize: '1em', color: '#fff', fontWeight: 300, whiteSpace: 'normal', textAlign: 'justify' }}>{proy.descripcion}</Typography>
                      </div>
                    </DevTextDescription>
                  </div>
                </div>

                <Hashtags >
                  {
                    (proy.hashtags.slice(0, this.props.isMobile ? 3 : proy.hashtags.length)).map((tag, index) => (
                      <Hashtag key={"" + index + tag}><Typography variant="subtitle1" style={{ fontSize: '0.9em', color: '#fff', fontWeight: 300, whiteSpace: 'normal', textAlign: 'center' }}>{tag}</Typography></Hashtag>
                    ))
                  }
                </Hashtags>

                {
                  proy.linkCodigo != "" ?
                    <BottomIcon left>
                      <a href={proy.linkCodigo} target="_blank" rel="noopener noreferrer" ><CodeOutlined style={{ color: '#2b2b2b', fontSize: 28 }} /></a>
                    </BottomIcon>
                    : null
                }
                {
                  proy.linkPagina != "" ?
                    <BottomIcon>
                      <a href={proy.linkPagina} target="_blank" rel="noopener noreferrer" ><LaptopMac style={{ color: '#2b2b2b', fontSize: 28 }} /></a>
                    </BottomIcon>
                    : null
                }


              </AnimatedCard>
            ))
          }

        </div>

        <div style={{ visibility: this.props.sectionFocus == 2 ? 'visible' : 'hidden', backgroundColor: 'transparent', width: '100%', height: '10%', position: 'absolute', bottom: 0 }}>
          <Stepper listLength={list.length} onSelect={(index) => this.handleFocus(index + 1)} selected={this.state.focusedElement - 1} />
        </div>
      </ProyectosDiv>
    )
  }
}

Proyectos.propTypes = {
  isMobile: PropTypes.bool.isRequired
};
Proyectos.defaultProps = {
};
