import React, { Component } from 'react';
import { Typography, Grid, Button } from '@material-ui/core/'
import styled, { keyframes } from 'styled-components'
import PropTypes from 'prop-types';

import KeyboardArrowLeft from '@material-ui/icons/KeyboardArrowLeft';
import KeyboardArrowRight from '@material-ui/icons/KeyboardArrowRight';

const aparecerAnimation = keyframes`
  from {
    opacity:0;
    transform: rotate(-45deg) translate(0px,-15px);
  }
  to {
    opacity:1;
    transform: rotate(0) translate(0px,0px);
  }
`
const activeAnimation = keyframes`
  0% {
    transform: translate(0px,0px);
  }
  50% {
    transform: rotate(15deg) translate(0px,-10px);
  }
  100% {
    transform: translate(0px,0px);
  }
`
const inactiveAnimation = keyframes`
  0% {
    transform: translate(0px,0px);
  }
  50% {
    transform: rotate(-15deg) translate(0px,-10px);
  }
  100% {
    transform: translate(0px,0px);
  }
`
const ProgressSingleBar = styled.div`
  display: inline-block;
  width:25px;
  height:5px;
  cursor: pointer;
  border-radius:2px;
  margin: 15px 15px 0 15px;
  background-color: ${props => props.active ? 'rgba(0,0,0,0.7)' : 'rgba(0,0,0,0.2)'};
  opacity:0;
  transition: all 0.5s;  
  animation: ${aparecerAnimation} 2s ease, ${props => props.active ? activeAnimation : inactiveAnimation} 2s  ease-in-out;
  animation-delay: 0s;
  animation-fill-mode: forwards, none; 
`
const ButtonAparecer = styled(Button)`
  background-color: transparent !important;
  animation: ${aparecerAnimation} 3s ease;
  animation-fill-mode: forwards; 
`
const ButtonIconRight = styled(KeyboardArrowRight)`
  &:active{
    transform: rotate(30deg) translateX(30px) !important;
  }
  &:hover{
    transform: rotate(360deg);
    transition: all 1s ease;
  }
`
const ButtonIconLeft = styled(KeyboardArrowLeft)`
  &:active{
    transform: rotate(-30deg) translateX(-30px) !important;
  }
  &:hover{
    transform: rotate(-360deg);
    transition: all 1s ease;
  }
`

export default class Stepper extends Component {
  constructor(props) {
    super(props);

    this.state = {
      isProgressBarsOverflowing: false
    };
  }

  componentDidMount() {
    const isProgressBarsOverflowing = this.progressBars.offsetWidth < this.progressBars.scrollWidth;
    this.setState({ isProgressBarsOverflowing: isProgressBarsOverflowing })
  }

  selectItem(index) {
    this.props.onSelect(index)
  }

  render() {
    return (
      <div style={{ display:'flex', flexDirection:'row', justifyContent:'space-evenly', alignItems:'center', width: '100%' }}>
        <ButtonAparecer size="large" disableRipple={true}
          onClick={() => this.selectItem(this.props.selected - 1)}
          disabled={this.props.selected == 0}>
          <ButtonIconLeft fontSize='large' style={{ color: this.props.selected > 0 ? 'rgba(0,0,0,0.7)' : 'rgba(0,0,0,0.2)' }} />
        </ButtonAparecer>
        {
          !this.state.isProgressBarsOverflowing ?
            <div ref={(progressBars) => { this.progressBars = progressBars }}
              style={{
                overflow: 'hidden', whiteSpace: 'nowrap'
              }}>
              {
                Array.apply(null, { length: this.props.listLength }).map((value, index) => {
                  return <ProgressSingleBar key={index} active={index <= this.props.selected} onClick={() => this.selectItem(index)} delay={index / 2} />
                })
              }

            </div>
            : null
        }

        <ButtonAparecer size="large" disableRipple={true}  disableFocusRipple={true} 
          onClick={() => this.selectItem(this.props.selected + 1)}
          disabled={this.props.selected == this.props.listLength - 1}>
          <ButtonIconRight fontSize='large' style={{ color: this.props.selected < this.props.listLength - 1 ? 'rgba(0,0,0,0.7)' : 'rgba(0,0,0,0.2)' }} />
        </ButtonAparecer>
      </div>
    )
  }
}

Stepper.propTypes = {
  listLength: PropTypes.number.isRequired,
  selected: PropTypes.number.isRequired,
  onSelect: PropTypes.func.isRequired
};
Stepper.defaultProps = {
  listLength: 1,
  selected: 1
};